Y,iBAAgB;MAChB,iBAAgB;CAiBnB;AA5MT;QA6LgB,iBAAgB;QAChB,iCFrLM;QEsLN,mBFxKI;QEyKJ,eAAc;QACd,kBFxKC;CEkLJ;AA3Mb;UAmMoB,eFrLI;UEsLJ,mBF7KA;CEmLH;AA1MjB;YAsMwB,kBF1KN;YE2KM,eF1LA;YE2LA,kBAAiB;CACpB","file":"index.vue","sourcesContent":["@charset \"UTF-8\";\n/*\n@module 颜色\n*/\n/*背景色*/\n/*背景颜色名*/\n/*边框颜色*/\n/*字体颜色*/\n/*\n@module 字体大小\n*/\n/* 防止用户自定义背景颜色对网页的影响，添加让用户可以自定义字体 */\nhtml {\n  min-height: 100%;\n  -webkit-text-size-adjust: 100%;\n  -ms-text-size-adjust: 100%;\n  -webkit-overflow-scrolling: touch;\n  padding: constant(safe-area-inset-top) constant(safe-area-inset-right) constant(safe-area-inset-bottom) constant(safe-area-inset-left); }\n\n/* 内外边距通常让各个浏览器样式的表现位置不同 */\nbody,\ndiv,\ndl,\ndt,\ndd,\nul,\nol,\nli,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\npre,\ncode,\nform,\nfieldset,\nlegend,\ninput,\ntextarea,\np,\nblockquote,\nth,\ntd,\nhr,\nbutton,\narticle,\naside,\ndetails,\nfigcaption,\nfigure,\nfooter,\nheader,\nhgroup,\nmenu,\nnav,\nsection,\n* {\n  margin: 0;\n  padding: 0; }\n\n/* 重设 HTML5 标签, IE 需要在 js 中 createElement(TAG) */\narticle,\naside,\ndetails,\nfigcaption,\nfigure,\nfooter,\nheader,\nhgroup,\nmenu,\nnav,\nsection {\n  display: block; }\n\n/* HTML5 媒体文件跟 img 保持一致 */\naudio,\ncanvas,\nvideo {\n  display: inline-block;\n  *display: inline;\n  *zoom: 1; }\n\n/* 要注意表单元素并不继承父级 font 的问题 */\nbody,\nbutton,\ninput,\nselect,\ntextarea {\n  font-style: normal;\n  font-size: 0.24rem;\n  font-family: \\5FAE\\8F6F\\96C5\\9ED1, Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, tahoma, arial, \"Hiragino Sans GB\", \\5b8b\\4f53, sans-serif;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n  color: #fff; }\n\ninput,\nselect,\ntextarea {\n  font-size: 100%; }\n\n/* 去掉各Table  cell 的边距并让其边重合 */\ntable {\n  border-collapse: collapse;\n  border-spacing: 0; }\n\n/* IE bug fixed: th 不继承 text-align*/\nth {\n  text-align: inherit; }\n\n/* 去除默认边框 */\nfieldset,\nimg {\n  border: 0; }\n\n/* ie6 7 8(q) bug 显示为行内表现 */\niframe {\n  display: block; }\n\